import React, { useState } from 'react'
import PetPaymentModal from './PetPaymentModal'

const PetDocumentAndPayment = (props) => {
    const [openPaymentModal, setOpenPaymentModal] = useState(0)

    return (
        <>
            <PetPaymentModal openPaymentModal={openPaymentModal} />
            <div className='m-5'>
                <table className='w-full text-left text-gray-900'>
                    <thead className='text-lg bg-gray-300 border-b-2'>
                        <tr className=''>
                            <th className='px-3 py-2'>Sl.</th>
                            <th className='px-3 py-2'>Document Name</th>
                            <th className='px-3 py-2'>Status</th>
                            <th className='px-3 py-2'>Action</th>
                        </tr>
                    </thead>
                    <tbody className=''>

                        <tr className='hover:bg-sky-100 border-b'>
                            <td className='px-3 py-2'>1.</td>
                            <td className='px-3 py-2'>Rabies Vaccination certificate</td>
                            <td className='px-3 py-2'>Not Upload</td>
                            <td className='px-3 py-2'><button className='px-2 py-1 border border-indigo-600 rounded shadow hover:bg-indigo-600 text-black hover:text-white'>Upload</button></td>
                        </tr>
                        <tr className='hover:bg-sky-100 border-b'>
                            <td className='px-3 py-2'>2.</td>
                            <td className='px-3 py-2'>Vaccination certificate of Leptospirosis </td>
                            <td className='px-3 py-2'>Not Upload</td>
                            <td className='px-3 py-2'><button className='px-2 py-1 border border-indigo-600 rounded shadow hover:bg-indigo-600 text-black hover:text-white'>Upload</button></td>
                        </tr>
                        <tr className='hover:bg-sky-100 border-b'>
                            <td className='px-3 py-2'>3.</td>
                            <td className='px-3 py-2'>Address proof </td>
                            <td className='px-3 py-2'>Not Upload</td>
                            <td className='px-3 py-2'><button className='px-2 py-1 border border-indigo-600 rounded shadow hover:bg-indigo-600 text-black hover:text-white'>Upload</button></td>
                        </tr>
                        <tr className='hover:bg-sky-100 border-b'>
                            <td className='px-3 py-2'>4.</td>
                            <td className='px-3 py-2'>Rent Agreement</td>
                            <td className='px-3 py-2'>Not Upload</td>
                            <td className='px-3 py-2'><button className='px-2 py-1 border border-indigo-600 rounded shadow hover:bg-indigo-600 text-black hover:text-white'>Upload</button></td>
                        </tr>
                        <tr className='hover:bg-sky-100 border-b'>
                            <td className='px-3 py-2'>5.</td>
                            <td className='px-3 py-2'>NOC from the owner</td>
                            <td className='px-3 py-2'>Not Upload</td>
                            <td className='px-3 py-2'><button className='px-2 py-1 border border-indigo-600 rounded shadow hover:bg-indigo-600 text-black hover:text-white'>Upload</button></td>
                        </tr>
                    </tbody>
                </table>

            </div>

            <div>

                <button onClick={() => setOpenPaymentModal(prev => prev + 1)} type='submit' className='bg-indigo-700 px-8 py-2 text-white rounded font-sans font-semibold shadow'>Pay ₹ 1000 /-</button>
            </div>

            <div className='flex justify-center my-10 space-x-5'>
                <button type='button' onClick={() => props.screen(1)} className='bg-red-500 px-8 py-2 text-white rounded shadow'>Back</button>
                <button type='submit' className='bg-indigo-700 px-8 py-2 text-white rounded shadow'>Submit</button>
            </div>
        </>
    )
}

export default PetDocumentAndPayment