import React from 'react'
import { useFormik, Formik, Form, ErrorMessage } from 'formik'
import * as yup from 'yup'

const PetRegistrationFormIndex = (props) => {

  const validationSchema = yup.object({
    ulb: yup.string().required('Require'),
    applicantName: yup.string().required('Require'),
  })
  const initialValues = {
    ulb: '',
    applicantName: '',
  }
  const formik = useFormik({
    initialValues: initialValues,
    enableReinitialize: true,
    onSubmit: (values, resetForm) => {
      console.log("Value.....", values)
      // props.data(values)
      props.screen(2)
    },
    validationSchema
  })
  const handleChange = (event) => {
    let name = event.target.name
    let value = event.target.value
    // { name === 'propertyType' && ((value == '1') ? setpropertyTypeStatusToggle(true) : setpropertyTypeStatusToggle(false)) }
    // { name == 'mobileNo' && formik.setFieldValue("mobileNo", allowNumberInput(value, formik.values.mobileNo, 10)) }
  };

  return (
    <>
      <form onSubmit={formik.handleSubmit} onChange={handleChange}>
        <div className=''>

          <div className='col-span-12 my-5 m-3'>
            <div className='text-2xl text-center text-gray-600 font-semibold'>Property Details</div>
            <p className='border-b border-gray-500'></p>
          </div>

          <div className='grid grid-cols-1 md:grid-cols-5 '>

            <div className='m-3'>
              <label htmlFor="ulb">Select ULB <span className='text-red-700 font-semibold'>*</span></label>
              <select {...formik.getFieldProps('ulb')} name='ulb' className='block w-full h-9 border px-2 border-gray-600 rounded shadow'>
                <option value="">Select</option>
                <option value="1">ULB 1</option>
                <option value="2">ULB 2</option>
                <option value="3">ULB 3</option>
              </select>
              <p className='text-red-500 absolute text-xs'>{formik.touched.ulb && formik.errors.ulb ? formik.errors.ulb : null}</p>
            </div>

            <div className='m-3'>
              <label htmlFor="applyThrough">Apply Through<span className='text-red-700 font-semibold'>*</span></label>
              <select {...formik.getFieldProps('applyThrough')} name='applyThrough' className='block w-full h-9 border px-2 border-gray-600 rounded shadow'>
                <option value="">Select</option>
                <option value="holdingApply">Holding </option>
                <option value="safApply">SAF </option>
                <option value="ownerApply">Other</option>
              </select>
              <p className='text-red-500 absolute text-xs'>{formik.touched.applyThrough && formik.errors.applyThrough ? formik.errors.applyThrough : null}</p>
            </div>

            <div className='m-3'>
              <label htmlFor="holdingNo">Holding / SAF No.<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('holdingNo')} type="text" name='holdingNo' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 absolute text-xs'>{formik.touched.holdingNo && formik.errors.holdingNo ? formik.errors.holdingNo : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="categoryApplication">Category of Application<span className='text-red-700 font-semibold'>*</span></label>
              <select {...formik.getFieldProps('categoryApplication')} name='categoryApplication' className='block w-full h-9 border px-2 border-gray-600 rounded shadow'>
                <option value="">Select</option>
                <option value="owner">Owner</option>
                <option value="tenanted">Tenanted</option>
              </select>
              <p className='text-red-500 absolute text-xs'>{formik.touched.categoryApplication && formik.errors.categoryApplication ? formik.errors.categoryApplication : null}</p>
            </div>

            <div className='m-3'>
              <label htmlFor="ward">Ward Number<span className='text-red-700 font-semibold'>*</span></label>
              <select {...formik.getFieldProps('ward')} name='ward' className='block w-full h-9 border px-2 border-gray-600 rounded shadow'>
                <option value="">Select</option>
                <option value="1">Ward 1</option>
                <option value="2">Ward 2</option>
                <option value="3">Ward 3</option>
              </select>
            </div>
          </div>

          <div className='col-span-12 my-5 m-3'>
            <div className='text-2xl text-center text-gray-600 font-semibold'>Applicant Details</div>
            <p className='border-b border-gray-500'></p>
          </div>

          <div className='grid grid-cols-1 md:grid-cols-4'>

            <div className='m-3'>
              <label htmlFor="applicantName">Name of Applicant<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('applicantName')} type="text" name='applicantName' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.applicantName && formik.errors.applicantName ? formik.errors.applicantName : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="mobileNo">Mobile No<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('mobileNo')} type="text" name='mobileNo' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.mobileNo && formik.errors.mobileNo ? formik.errors.mobileNo : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="email">Email<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('email')} type="email" name='email' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.email && formik.errors.email ? formik.errors.email : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="panNo">PAN No.<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('panNo')} type="text" name='panNo' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.panNo && formik.errors.panNo ? formik.errors.panNo : null}</p>
            </div>
            {/* <div className='m-3'>
              <label htmlFor="aadharNo">Aadhar No.<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('aadharNo')} type="text" name='aadharNo' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.aadharNo && formik.errors.aadharNo ? formik.errors.aadharNo : null}</p>
            </div> */}

          </div>

          <div className='m-3'>
            <label htmlFor="address">Address<span className='text-red-700 font-semibold'>*</span></label>
            <textarea {...formik.getFieldProps('address')} name='address' rows="3" className='block w-full border px-2 border-gray-600 rounded shadow' ></textarea>
            <p className='text-red-500 text-xs'>{formik.touched.address && formik.errors.address ? formik.errors.address : null}</p>
          </div>

          <div className='col-span-12 my-5 m-3'>
            <div className='text-2xl text-center text-gray-600 font-semibold'>Dog Details</div>
            <p className='border-b border-gray-500 mt-3'></p>
          </div>

          <div className='grid grid-cols-1 md:grid-cols-4'>

            <div className='m-3'>
              <label htmlFor="petName">Name of Dog<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('petName')} type="text" name='petName' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.petName && formik.errors.petName ? formik.errors.petName : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="petGender">Gender<span className='text-red-700 font-semibold'>*</span></label>
              <select {...formik.getFieldProps('petGender')} type="text" name='petGender' className='block w-full h-9 border px-2 border-gray-600 rounded shadow'>
                <option value="">Select</option>
                <option value="male">Male</option>
                <option value="female">Female</option>
              </select>
              <p className='text-red-500 text-xs'>{formik.touched.petGender && formik.errors.petGender ? formik.errors.petGender : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="dogBirthDate">Date of Birth<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('dogBirthDate')} type="date" name='dogBirthDate' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.dogBirthDate && formik.errors.dogBirthDate ? formik.errors.dogBirthDate : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="dogIdentity">Identity Mart<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('dogIdentity')} type="text" name='dogIdentity' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.dogIdentity && formik.errors.dogIdentity ? formik.errors.dogIdentity : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="breed">Breed<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('breed')} type="text" name='breed' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.breed && formik.errors.breed ? formik.errors.breed : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="color">Color<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('color')} type="text" name='color' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.color && formik.errors.color ? formik.errors.color : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="doctorName">Veterinary Doctor Name<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('doctorName')} type="text" name='doctorName' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.doctorName && formik.errors.doctorName ? formik.errors.doctorName : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="doctorRegNo">Doctor’s MSVC/VCI registration number <span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('doctorRegNo')} type="text" name='doctorRegNo' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.doctorRegNo && formik.errors.doctorRegNo ? formik.errors.doctorRegNo : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="dateOfRabies">Date of Rabies<span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('dateOfRabies')} type="date" name='dateOfRabies' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.dateOfRabies && formik.errors.dateOfRabies ? formik.errors.dateOfRabies : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="dateOfLepVaccine">Date of Leptospirosis Vaccination <span className='text-red-700 font-semibold'>*</span></label>
              <input {...formik.getFieldProps('dateOfLepVaccine')} type="text" name='dateOfLepVaccine' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' />
              <p className='text-red-500 text-xs'>{formik.touched.dateOfLepVaccine && formik.errors.dateOfLepVaccine ? formik.errors.dateOfLepVaccine : null}</p>
            </div>
            <div className='m-3'>
              <label htmlFor="dogFrom">Dog From<span className='text-red-700 font-semibold'>*</span></label>
              <select {...formik.getFieldProps('dogFrom')} type="text" name='dogFrom' className='block w-full h-9 border px-2 border-gray-600 rounded shadow' >
              <option value="">Select</option>
              <option value="transferred">Dog being transferred</option>
              <option value="bought">Dog being bought?</option>
              <option value="Adopted">Stray Dog Adopted</option>
              <option value="outside">Dog brought from outside Jharkhand</option>
              <option value="owners">Puppy born to owners Dog</option>
              </select>
              <p className='text-red-500 text-xs'>{formik.touched.dogFrom && formik.errors.dogFrom ? formik.errors.dogFrom : null}</p>
            </div>
          </div>

        </div>


        <div className='flex justify-center my-10'>
          <button type='submit' className='bg-indigo-700 px-8 py-2 text-white rounded shadow'>Next</button>
        </div>
      </form>


      {/* <form onSubmit={formik.handleSubmit} onChange={handleChange}>
                <div className="grid grid-cols-1 md:grid-cols-4">
                    <div className="col-span-4 grid grid-cols-1 md:grid-cols-4">
                        <div className={`${inputContainerStyle}`}>
                            <label className={`${inputLabelStyle}`}><small className="block mt-1 text-sm font-semibold text-red-600 inline ">*</small>Email address</label>
                            <input type="text" {...formik.getFieldProps('email')} className={`${inputStyle}`} />
                            <p className='text-red-500 text-xs'>{formik.touched.email && formik.errors.email ? formik.errors.email : null}</p>
                        </div>
                        <div className={`${inputContainerStyle}`}>
                            <label className={`${inputLabelStyle}`}><small className="block mt-1 text-sm font-semibold text-red-600 inline ">*</small>Password Through</label>
                            <input type="password" {...formik.getFieldProps('password')} className={`${inputStyle}`} />
                            <p className='text-red-500 text-xs'>{formik.touched.password && formik.errors.password ? formik.errors.password : null}</p>
                        </div>
                    </div>
                </div>
            </form> */}
    </>
  )
}

export default PetRegistrationFormIndex


