import React, { useState } from 'react'
import { PetAccordionPage } from './Form/PetAccordionPage'
import PetDocumentAndPayment from './Form/PetDocumentAndPayment'
import PetRegistrationFormIndex from './Form/PetRegistrationFormIndex'

const PetRegistrationIndex = () => {
  const [currentScreen, setCurrentScreen] = useState(1)

  console.log("currentScreen", currentScreen)

  return (
    <>
      <div className="grid grid-cols-12 space-x-10 my-10 mx-8">
        <div className='col-span-9 border border-gray-400 rounded p-5'>
          <p className="border-2 border-indigo-400 bg-indigo-200 p-2 text-center text-xl shadow-md">You are applying <span className='font-semibold'> Pet Registration </span></p>
          <p className={`${currentScreen == 1 ? 'block' : 'hidden'}`}> <PetRegistrationFormIndex screen={setCurrentScreen} /> </p>
          <p className={`${currentScreen == 2 ? 'block' : 'hidden'}`}> <PetDocumentAndPayment screen={setCurrentScreen} /></p>
        </div>
        <div className='col-span-3 border border-gray-400 p-3'>
          <p className="border-2 border-indigo-400 p-2 text-center bg-white text-xl shadow-md"> Frequently Asked Questions </p>
          <PetAccordionPage />
        </div>
      </div>
    </>
  )
}

export default PetRegistrationIndex